*,::after,::before{box-sizing:border-box;margin:0;padding:0}body{font-family:Monospaced Number,Chinese Quote,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,PingFang SC,Hiragino Sans GB,Microsoft YaHei,Helvetica Neue,Helvetica,Arial,sans-serif}iframe,img,input,select,textarea{border:0;outline:0}input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}button:focus,input:focus,select:focus,textarea:focus{outline:0}a,a:active,a:hover,a:link,a:visited{text-decoration:none;color:inherit}::-ms-clear{display:none}::-webkit-search-cancel-button,::-webkit-search-decoration,::-webkit-search-results-button,::-webkit-search-results-decoration{display:none}li,ul{list-style:none}.yoshino-pagination{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.yoshino-pagination-item{display:inline-block;min-width:32px;height:32px;line-height:32px;border:1px solid #d9d9d9;border-radius:4px;margin-right:8px;text-align:center;font-size:14px;cursor:pointer}.yoshino-pagination-item-active,.yoshino-pagination-item:hover{border-color:#51b26d;color:#51b26d}.yoshino-pagination-item-more{border:none}.yoshino-pagination-item:last-child{margin-right:0}.yoshino-pagination-footer,.yoshino-pagination-header{padding:0 5px}